import axios from 'axios'
import {encryptdata, decryptdata} from '../../../utility/context/SecurityTool'
axios.defaults.baseURL = 'https://lastarzi2.herokuapp.com'
//axios.defaults.baseURL = "http://localhost:5000";
export const getData = (params) => {
  return async (dispatch) => {
    let userData = localStorage.getItem('logInUserData')
      ? JSON.parse(localStorage.getItem('logInUserData'))
      : {}
    await axios
      .get('/backendapi/nominee/list/' + userData._id, params)
      .then((response) => {
        console.log('aaquib  123', response.data)
        console.log('aaquib  123', params)
        const res = response.data;
        for(let l=0; l<res.length > 0; l=l+1) {
          res[l].address = decryptdata(res[l].address,true);
          
          res[l].email = decryptdata(res[l].email,true);
          res[l].name = decryptdata(res[l].name,true);
          res[l].primaryContact = decryptdata(res[l].primaryContact,true);
          res[l].relation = decryptdata(res[l].relation,true);
          res[l].secondaryContact = decryptdata(res[l].secondaryContact,true);
        }
        dispatch({
          type: 'GET_DATA',
          data: res,
          totalPages: response.length,
          params
        })
      })
  }
}

